  color: #fff;
}
.form-footer__input:hover {
  box-shadow: 0 0 2px #fff;
}
.form-footer__input:focus {
  border-color: #fff;
}
.form-footer__btn {
  text-transform: uppercase;
  width: 40%;
}
.form-footer__btn:focus {
  border-color: #fff;
}

.bottom-footer__body {
  background: #0F0F0F;
  font-size: 12px;
  padding: 17px 0;
  gap: 10px;
}
.bottom-footer__agreement {
  text-decoration: underline;
}

@media (max-width: 991px) {
  .top-footer {
    padding: 40px 30px;
  }
  .top-footer__row {
    gap: 30px;
  }
  .top-footer__title {
    margin-bottom: 15px;
  }
  .top-footer__text {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .top-footer {
    padding: 40px 0;
  }
  .form-footer {
    gap: 10px;
  }
  .form-footer__input, .form-footer__btn {
    font-size: 18px;
    width: 100%;
  }
}